import "./App.css";
import react, { useEffect, useState } from "react";
import Alert from "@mui/material/Alert";
import Box from "@mui/material/Box";
import Stack from "@mui/material/Stack";
import Modal from "@mui/material/Modal";
import TextField from "@mui/material/TextField";
import axios from "axios";
import { styled } from "@mui/material/styles";
import DeleteIcon from "@mui/icons-material/Delete";
import SendIcon from "@mui/icons-material/Send";
import EditIcon from "@mui/icons-material/Edit";
import { v4 as uuidv4 } from "uuid";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { Button, CardActions } from "@mui/material";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

const inputStyle = {
  "& > :not(style):last-child": { m: 1, width: "100%", margin: "8px 0px" },
};

function App() {
  const [notas, setNotas] = useState([]);
  const [alert, setAlert] = useState();
  const [base64URL, setBase64URL] = useState("");
  const [statusChange, setStatusChange] = useState("");
  const [editId, setEditId] = useState();
  const [titleValue, setTitleValue] = useState("");
  const [stockValue, setStockValue] = useState("");
  const [descriptionValue, setDescriptionValue] = useState("");
  const [open, setOpen] = useState(false);
  const [openEdit, setOpenEdit] = useState(false);

  const handleOpen = () => setOpen(true);
  const handleClose = () => {
    setOpen(false);
    setTitleValue("");
    setDescriptionValue("");
  };
  const handleCloseEdit = () => {
    setOpenEdit(false);
    setTitleValue("");
    setDescriptionValue("");
  };

  function convertFileToBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        const base64String = reader.result
          .replace("data:", "")
          .replace(/^.+,/, "");
        resolve(base64String);
      };
      reader.onerror = (error) => reject(error);
    });
  }

  const handleFileUpload = async (event) => {
    const file = event.target.files[0];
    const base64String = await convertFileToBase64(file);
    setBase64URL(base64String);
  };

  const handleOnClickDelete = (id) => {
    const fetchData = async () => {
      try {
        const response = await axios({
          method: "delete",
          url:
            "https://5o7c4hvccg.execute-api.us-east-2.amazonaws.com/products/" +
            id,
        });
        setStatusChange("hasChangeDelete");
      } catch (error) {
        console.log(error);
      }
    };
    setStatusChange();
    fetchData();
  };

  const handleOnClickAdd = () => {
    if ((titleValue.length > 0) & (descriptionValue.length > 0)) {
      const fetchData = async () => {
        try {
          const response = await axios({
            method: "post",
            url: "https://5o7c4hvccg.execute-api.us-east-2.amazonaws.com/products",
            data: {
              id: uuidv4(),
              nameProducts: titleValue,
              Price: descriptionValue,
              imageBase64: base64URL,
              Stock: stockValue,
            },
          });
          setTitleValue("");
          setDescriptionValue("");
          setBase64URL("");
          setStockValue("");
          setOpen(false);
          setStatusChange("hasChangeAdd");
        } catch (error) {
          console.log(error);
        }
      };

      fetchData();
      setStatusChange();
    } else setAlert(true);
  };

  const handleOnClickEdit = (id) => {
    if ((titleValue.length > 0) & (descriptionValue.length > 0)) {
      const fetchData = async () => {
        try {
          const response = await axios({
            method: "put",
            url:
              "https://5o7c4hvccg.execute-api.us-east-2.amazonaws.com/products/" +
              id,
            data: {
              nameProducts: titleValue,
              Price: descriptionValue,
              imageBase64: base64URL,
              Stock: stockValue,
            },
          });
          setTitleValue("");
          setDescriptionValue("");
          setBase64URL("");
          setStockValue("");
          setOpenEdit(false);
          setStatusChange("hasChangeEdit");
        } catch (error) {
          console.log(error);
        }
      };
      setStatusChange();
      fetchData();
    } else setAlert(true);
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios({
          method: "get",
          url: "https://5o7c4hvccg.execute-api.us-east-2.amazonaws.com/products",
        });

        setNotas(response.data);
        setStatusChange("hasChangeGet");
      } catch (error) {
        console.log(error);
      }
      setStatusChange();
    };

    fetchData();
  });

  const handleChangeTitle = (event) => {
    setTitleValue(event.target.value);
    setAlert(false);
  };

  const handleChangeStock = (event) => {
    setStockValue(event.target.value);
    setAlert(false);
  };
  const handleChangeDescription = (event) => {
    setDescriptionValue(event.target.value);
    setAlert(false);
  };

  const handleOnClickEditModal = (id) => {
    setOpenEdit(true);
    setEditId(id);
  };

  return (
    <div className="App">
      <div className="Contenido">
        {notas.map((nota) => (
          <Card sx={{ maxWidth: 345, minWidth: 345 }}>
            <CardMedia
              component="img"
              height="350"
              image={nota.imageUrl}
              alt="Image"
            />
            <CardContent>
              <Typography gutterBottom variant="h5" component="div">
                {nota.nameProducts}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                $ {nota.Price}
              </Typography>
            </CardContent>
            <CardActions>
              <Typography gutterBottom variant="p" component="div">
                Stock {nota.Stock}
              </Typography>
              <Button
                size="small"
                color="primary"
                onClick={() => {
                  handleOnClickEditModal(nota.id);
                }}
              >
                <EditIcon />
              </Button>
              <Button
                size="small"
                color="primary"
                onClick={() => {
                  handleOnClickDelete(nota.id);
                }}
              >
                <DeleteIcon />
              </Button>
            </CardActions>
          </Card>
        ))}
      </div>
      <div className="AgregarContainer">
        <Button onClick={handleOpen} variant="contained">
          Agregar Nota
        </Button>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box component="form" sx={style} noValidate autoComplete="off">
            <h2 id="parent-modal-title">Agregar nota</h2>
            <Stack direction="column" spacing={1}>
              <Box
                component="form"
                sx={inputStyle}
                noValidate
                autoComplete="off"
              >
                <TextField
                  id="outlined-basic"
                  label="Titulo"
                  variant="outlined"
                  value={titleValue}
                  onChange={handleChangeTitle}
                />
                <TextField
                  id="outlined-basic"
                  label="Stock"
                  variant="outlined"
                  value={stockValue}
                  onChange={handleChangeStock}
                />
                <TextField
                  id="outlined-basic"
                  label="Precio"
                  variant="outlined"
                  value={descriptionValue}
                  onChange={handleChangeDescription}
                />
                <div>
                  <input
                    type="file"
                    name="file"
                    accept=".jpg"
                    onChange={handleFileUpload}
                  />
                </div>
              </Box>
              <Button
                onClick={handleOnClickAdd}
                variant="contained"
                endIcon={<SendIcon />}
              >
                Agregar
              </Button>
              {alert ? (
                <Alert severity="error">
                  Por favor digita los campos obligatorios
                </Alert>
              ) : null}
            </Stack>
          </Box>
        </Modal>
        <Modal
          open={openEdit}
          onClose={handleCloseEdit}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box component="form" sx={style} noValidate autoComplete="off">
            <h2 id="parent-modal-title">Editar nota</h2>
            <Stack direction="column" spacing={1}>
              <Box
                component="form"
                sx={inputStyle}
                noValidate
                autoComplete="off"
              >
                <TextField
                  id="outlined-basic"
                  label="Titulo"
                  variant="outlined"
                  value={titleValue}
                  onChange={handleChangeTitle}
                />
                <TextField
                  id="outlined-basic"
                  label="Stock"
                  variant="outlined"
                  value={stockValue}
                  onChange={handleChangeStock}
                />
                <TextField
                  id="outlined-basic"
                  label="Precio"
                  variant="outlined"
                  value={descriptionValue}
                  onChange={handleChangeDescription}
                />
                <div>
                  <input
                    type="file"
                    name="file"
                    accept=".jpg"
                    onChange={handleFileUpload}
                  />
                </div>
              </Box>
              <Button
                onClick={() => handleOnClickEdit(editId)}
                variant="contained"
                endIcon={<SendIcon />}
              >
                Editar
              </Button>
              {alert ? (
                <Alert severity="error">
                  Por favor digita los campos obligatorios
                </Alert>
              ) : null}
            </Stack>
          </Box>
        </Modal>
      </div>
    </div>
  );
}

export default App;
